import React, { useRef, useState, useEffect } from 'react';
import { transformarLinkGoogleDrive } from '../../../utils/midia';

/**
 * Componente para exibição de documentos PDF com suporte a Google Drive e fallback
 * @param {Object} props
 * @param {string} props.documentUrl - URL do documento PDF
 * @param {string} props.title - Título do documento (para acessibilidade)
 * @param {string} props.className - Classes CSS adicionais
 */
const PDFViewer = ({ documentUrl, title = "Documento PDF", className = "" }) => {
  const iframeRef = useRef(null);
  const [iframeError, setIframeError] = useState(false);
  const [useGoogleDocsViewer, setUseGoogleDocsViewer] = useState(false);

  useEffect(() => {
    const iframe = iframeRef.current;
    if (!iframe) return;

    const handleLoad = () => {
      setIframeError(false);
    };

    const handleError = () => {
      setIframeError(true);
      setUseGoogleDocsViewer(true);
    };

    iframe.addEventListener('load', handleLoad);
    iframe.addEventListener('error', handleError);

    return () => {
      iframe.removeEventListener('load', handleLoad);
      iframe.removeEventListener('error', handleError);
    };
  }, [documentUrl]);

  if (!documentUrl) {
    return null;
  }

  const isGoogleDriveLink = documentUrl.includes('drive.google.com/file/d/');

  if (!isGoogleDriveLink) {
    return (
      <div className="flex justify-center">
        <a
          href={documentUrl}
          target="_blank"
          rel="noopener noreferrer"
          className="inline-flex items-center px-6 py-3 text-base font-medium text-white bg-green-700 hover:bg-green-800 rounded-lg transition-colors duration-200 shadow-md hover:shadow-lg"
        >
          <svg 
            className="w-5 h-5 mr-2" 
            fill="none" 
            stroke="currentColor" 
            viewBox="0 0 24 24"
          >
            <path 
              strokeLinecap="round" 
              strokeLinejoin="round" 
              strokeWidth={2} 
              d="M12 10v6m0 0l-3-3m3 3l3-3m2 8H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z" 
            />
          </svg>
          Ver documento PDF
        </a>
      </div>
    );
  }

  return (
    <div className={`rounded-lg overflow-hidden shadow border border-green-300 ${className}`}>
      {!useGoogleDocsViewer ? (
        <iframe 
          ref={iframeRef}
          src={documentUrl.replace('/view?usp=sharing', '/preview')}
          width="100%" 
          height="500px"
          allow="autoplay"
          loading="lazy"
          title={title}
          sandbox="allow-same-origin allow-scripts allow-popups allow-forms"
        />
      ) : (
        <iframe
          ref={iframeRef}
          src={transformarLinkGoogleDrive(documentUrl)}
          width="100%"
          height="500px"
          allow="autoplay"
          loading="lazy"
          title={`${title} (Google Docs Viewer)`}
          sandbox="allow-same-origin allow-scripts allow-popups allow-forms"
        />
      )}
      {iframeError && (
        <div className="p-4 text-center text-gray-600 bg-gray-50">
          <p className="mb-2">Não foi possível carregar o documento diretamente.</p>
          <a
            href={documentUrl}
            target="_blank"
            rel="noopener noreferrer"
            className="text-green-600 hover:text-green-800 underline"
          >
            Abrir em nova aba
          </a>
        </div>
      )}
    </div>
  );
};

export default PDFViewer; 