import React from 'react';

/**
 * Componente que exibe o painel introdutório do site
 * @param {Object} props
 * @param {Object} props.painelInfo - Informações do painel introdutório
 */
const IntroPanel = ({ painelInfo }) => {
  if (!painelInfo?.descricao) return null;

  return (
    <div className="space-y-4">
      <h2 className="text-2xl font-bold text-green-800">{painelInfo.titulo}</h2>
      <div className="prose prose-lg max-w-none">
        <div className="text-gray-700 whitespace-pre-line">{painelInfo.descricao}</div>
      </div>
    </div>
  );
};

export default IntroPanel; 