import React from 'react';
import InfoSection from './InfoSection';
import InfoItem from './InfoItem';
import BooleanValue from './BooleanValue';
import MapLink from './MapLink';

/**
 * Componente que exibe informações sobre uma Terra Indígena
 * @param {Object} props
 * @param {Object} props.terraIndigena - Dados da terra indígena
 */
const TerraIndigenaInfo = ({ terraIndigena }) => {
  if (!terraIndigena) return null;

  return (
    <>
      <h2 className="text-2xl font-bold text-green-800 mb-4">{terraIndigena.nome}</h2>
      
      <InfoSection title="Informações Gerais">
        <InfoItem label="Nome" value={terraIndigena.nome} />
        <InfoItem label="Povo" value={terraIndigena.povo} />
        <InfoItem label="Estado" value={terraIndigena.estado} />
        <InfoItem label="Município" value={terraIndigena.municipio} />
        <InfoItem 
          label="Localização" 
          value={<MapLink 
            latitude={terraIndigena.latitude} 
            longitude={terraIndigena.longitude}
            nome={terraIndigena.nome}
          />} 
        />
        <InfoItem 
          label="Demarcada" 
          value={<BooleanValue value={terraIndigena.demarcada} />} 
        />
        <InfoItem 
          label="Homologada" 
          value={<BooleanValue value={terraIndigena.homologada} />} 
        />
      </InfoSection>

      {terraIndigena.descricao && (
        <InfoSection title="Descrição">
          <p className="text-gray-700 whitespace-pre-line">{terraIndigena.descricao}</p>
        </InfoSection>
      )}
    </>
  );
};

export default TerraIndigenaInfo; 