/**
 * Utilitários para manipulação de mídia e URLs
 */

/**
 * Extrai o ID do vídeo de uma URL do YouTube
 * @param {string} url - URL do vídeo do YouTube
 * @returns {string|null} ID do vídeo ou null se inválido
 */
export const extrairIdYoutube = (url) => {
  if (!url || typeof url !== 'string') {
    return null;
  }

  // Remove parâmetros de query para simplificar (tudo após '?')
  const baseUrl = url.split('?')[0];

  // Padrões para pegar o ID de vídeo
  const patterns = [
    /youtube\.com\/embed\/([^/?&]+)/,                 // embed/ID
    /youtube\.com\/watch\?v=([^&]+)/,                 // watch?v=ID
    /youtu\.be\/([^?&]+)/,                            // youtu.be/ID
    /youtube\.com\/v\/([^?&]+)/                       // youtube.com/v/ID
  ];

  for (const pattern of patterns) {
    const match = baseUrl.match(pattern);
    if (match && match[1]) {
      return match[1];
    }
  }

  return null;
};

/**
 * Transforma um link do Google Drive em um link para visualização via Google Docs Viewer
 * @param {string} link - Link do Google Drive
 * @returns {string|null} Link para visualização ou null se inválido
 */
export const transformarLinkGoogleDrive = (link) => {
  if (!link || typeof link !== 'string') return null;
  
  // Tenta extrair o fileId do link do Google Drive
  const match = link.match(/\/d\/([a-zA-Z0-9-_]+)/);
  if (!match) return null;
  
  const fileId = match[1];
  return `https://docs.google.com/gview?url=https://drive.google.com/uc?id=${fileId}&embedded=true`;
}; 