import React from 'react';
import IntroPanel from './IntroPanel';
import TerraIndigenaInfo from './TerraIndigenaInfo';
import EscolaInfo from './EscolaInfo/index';
import PDFViewer from './PDFViewer';
import YouTubePlayer from './YouTubePlayer';

/**
 * Componente que gerencia a exibição do conteúdo apropriado baseado no tipo de painel
 * @param {Object} props
 * @param {Object} props.painelInfo - Informações do painel
 * @param {string} props.painelInfo.tipo - Tipo do painel ('terra_indigena', 'escola', etc)
 * @param {string} props.painelInfo.titulo - Título do painel
 * @param {string} props.painelInfo.link_para_documentos - URL do documento PDF
 * @param {string} props.painelInfo.link_para_videos - URL do vídeo do YouTube
 * @param {boolean} props.isMaximized - Indica se o painel está maximizado
 */
const PainelContentSwitcher = ({ painelInfo, isMaximized }) => {
  if (!painelInfo) {
    return null;
  }

  const isTerraIndigena = painelInfo.tipo === 'terra_indigena';
  const isIntro = painelInfo.titulo === 'Sobre o site';

  // Classes para os cards
  const cardClasses = `
    bg-white backdrop-blur-sm
    rounded-xl shadow-md
    border border-green-100
    transition-all duration-100
    hover:shadow-lg hover:border-green-200
    p-6
  `;

  const mainContent = isIntro
    ? <IntroPanel painelInfo={painelInfo} />
    : isTerraIndigena
      ? <TerraIndigenaInfo terraIndigena={painelInfo} />
      : <EscolaInfo escola={painelInfo} />;

  return (
    <div className={isMaximized ? 'grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6' : 'space-y-6'}>
      {mainContent}

      {!isIntro && !isTerraIndigena && (
        <>
          {painelInfo.link_para_documentos && (
            <div className={cardClasses}>
              <h3 className="text-lg font-semibold text-green-800 mb-4 flex items-center gap-2">
                <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 12h6m-6 4h6m2 5H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z" />
                </svg>
                Produções e materiais da escola
              </h3>
              <PDFViewer 
                documentUrl={painelInfo.link_para_documentos}
                title="Documento PDF da escola"
              />
            </div>
          )}

          {painelInfo.link_para_videos && (
            <div className={cardClasses}>
              <h3 className="text-lg font-semibold text-green-800 mb-4 flex items-center gap-2">
                <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 10l4.553-2.276A1 1 0 0121 8.618v6.764a1 1 0 01-1.447.894L15 14M5 18h8a2 2 0 002-2V8a2 2 0 00-2-2H5a2 2 0 00-2 2v8a2 2 0 002 2z" />
                </svg>
                Vídeo da escola
              </h3>
              <YouTubePlayer 
                videoUrl={painelInfo.link_para_videos}
                title="Vídeo da escola"
              />
            </div>
          )}
        </>
      )}
    </div>
  );
};

export default PainelContentSwitcher; 