import React from 'react';
import { extrairIdYoutube } from '../../../utils/midia';

/**
 * Componente para exibição de vídeos do YouTube com tratamento de erros
 * @param {Object} props
 * @param {string} props.videoUrl - URL do vídeo do YouTube
 * @param {string} props.title - Título do vídeo (para acessibilidade)
 * @param {string} props.className - Classes CSS adicionais
 */
const YouTubePlayer = ({ videoUrl, title = "Vídeo do YouTube", className = "" }) => {
  if (!videoUrl) {
    return null;
  }

  const videoId = extrairIdYoutube(videoUrl);
  
  if (!videoId) {
    return (
      <div className={`p-4 text-center text-gray-600 bg-gray-50 rounded-lg ${className}`}>
        <p className="mb-2">Link do vídeo inválido ou não suportado.</p>
        <a
          href={videoUrl}
          target="_blank"
          rel="noopener noreferrer"
          className="text-green-600 hover:text-green-800 underline"
        >
          Tentar abrir no YouTube
        </a>
      </div>
    );
  }

  return (
    <div className={`space-y-2 ${className}`}>
      <div className="rounded-lg overflow-hidden shadow-lg border border-green-300">
        <div className="relative pb-[56.25%] h-0">
          <iframe
            className="absolute top-0 left-0 w-full h-full"
            src={`https://www.youtube.com/embed/${videoId}`}
            title={title}
            frameBorder="0"
            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
            allowFullScreen
            loading="lazy"
            referrerPolicy="origin"
          />
        </div>
      </div>
      <div className="text-sm text-gray-600 text-center">
        <a
          href={videoUrl}
          target="_blank"
          rel="noopener noreferrer"
          className="text-green-600 hover:text-green-800 hover:underline"
        >
          Ver vídeo no YouTube
        </a>
      </div>
    </div>
  );
};

export default YouTubePlayer; 